import React from "react";
import Img1 from "../../../assets/news/01.png";
import Img2 from "../../../assets/news/02.png";
import Img3 from "../../../assets/news/03(1).png";
import Img4 from "../../../assets/news/04.png";
import Img5 from "../../../assets/news/05.png";
import styled from "./style.module.css";
import { WrapperContainer } from "../../../App-styled";
function FooterLogos() {
  return (
    <>
      <WrapperContainer>
        <div className={styled.Wrapper}>
          <img src={Img1} alt="photo" />
          <img src={Img2} alt="photo" />
          <img src={Img3} alt="photo" />
          <img src={Img4} alt="photo" />
          <img src={Img5} alt="photo" />
        </div>
      </WrapperContainer>
    </>
  );
}

export default FooterLogos;
